.btn{border-radius:4px;background-color:#5ca1e1;border:none;color:#fff;text-align:center;font-size:18px;padding:16px;max-width:220px;transition:all .5s;cursor:pointer;margin:36px 20%;box-shadow:0 10px 20px -8px rgba(0,0,0,.7)}.btn{cursor:pointer;display:inline-block;position:relative;transition:.5s}.btn:after{content:"»";position:absolute;opacity:0;top:14px;right:-20px;transition:.5s}.btn:hover{padding-right:24px;padding-left:8px}.btn:hover:after{opacity:1;right:10px}